
.Articles {
  font-size: 14px;
  padding: 1em 0.5em;
  position: relative;
}
.Articles-info {
  background-color: #fff;
  color: var(--brand-blue-regal);
  padding: 5px;
  width: 50%;
}
.Articles-info-wrapper {
  overflow: auto;
}
.Articles-info__title {
  color: var(--brand-blue-pelorous);
  font-size: 18px;
  padding: 5px 0;
  text-transform: uppercase;
}
.Articles-info__heading {
  color: var(--brand-blue-regal);
  line-height: 2;
  font-size: 20px;
}
.Articles-info__list {
  list-style-position: inside;
}
.Articles-info__list-item {
  list-style-type: disclosure-closed;
  margin: 0;
}

.Articles__list {
  overflow: auto;
}
.Articles__item {
  margin: 10px;
  overflow: hidden;
}
.Articles__image {
  height: 170px;
  overflow: hidden;
  border-radius: 5px;
}
.Articles__thumbnail {}
.Articles__excerpt {}
.Articles__title {
  font-size: 16px;
}
.Articles__title-heading {}
.Articles__text {}

.Articles-media {
  width: 50%;
}
.Articles-media__source {}
.Articles-media__source iframe {
  background-color: var(--greyscale-grey-lightSlate);
  width: 100%;
}
@media screen and (min-width: 720px) {
  .Articles {
    align-items: stretch;
    display: flex;
    padding: 2em 15%;
    justify-content: space-between;
  }

  .Articles-info-wrapper {
    margin: 1.5em 0 0;
    padding: 0 1em;
  }

  .Articles-info__list {
    padding: 5px 0;
  }

  .Articles__list {
    display: flex;
    flex-wrap: wrap;
    margin: 3em 0 0;
  }

  .Articles__item {
    width: 40%;
    margin: 1em;
  }
}
